.login-box {
    position: fixed;
    z-index: 9999;
    border: 1px solid #ccc;
    padding: 20px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 500px;
    background-color: #3e8e41;
}

.login-box__title {
    margin: 0;
    font-size: 24px;
}

.login-box__button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.login-box__button:hover {
    background-color: #3e8e41;
}
